.modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0,0,0,0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1200;
}

.modal-content {
  background: #fff;
  padding: 2rem;
  border-radius: 12px;
  width: 90%;
  max-width: 500px;
  box-shadow: 0 6px 20px rgba(0,0,0,0.2);
  position: relative;
  overflow-y: auto;
  max-height: 90vh;
}

.modal-content h2 {
  margin-bottom: 1rem;
  font-size: 1.5rem;
  text-align: center;
}

.modal-content form {
  margin-bottom: 1.2rem;
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
}

.modal-content input {
  padding: 0.7rem;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 0.95rem;
}

.modal-content button {
  padding: 0.8rem;
  background: #007bff;
  color: #fff;
  font-size: 0.95rem;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.2s;
}

.modal-content button:hover {
  background: #0056b3;
}

.remove-btn {
  background: #dc3545 !important;
}

.remove-btn:hover {
  background: #b02a37 !important;
}

.close {
  position: absolute;
  top: 12px;
  right: 15px;
  cursor: pointer;
  font-size: 1.2rem;
}

.inline-icon {
  margin-right: 6px;
  vertical-align: middle;
}

.status-msg {
  text-align: center;
  font-size: 0.9rem;
  margin-bottom: 1rem;
  color: green;
}

/* 🔹 Responsive */
@media (max-width: 600px) {
  .modal-content {
    padding: 1rem;
    width: 95%;
  }

  .modal-content h2 {
    font-size: 1.2rem;
  }

  .modal-content input, 
  .modal-content button {
    font-size: 0.85rem;
    padding: 0.6rem;
  }
}
